import React, { useState, useCallback, useMemo } from 'react';
import { 
  Eventcalendar, 
  setOptions, 
  Popup, 
  Button, 
  Switch, 
  Datepicker,
  SegmentedGroup,
  SegmentedItem,
  Input,
  Dropdown,
  Textarea
} from '@mobiscroll/react';
import '@mobiscroll/react/dist/css/mobiscroll.min.css';

setOptions({
  theme: 'ios',
  themeVariant: 'light'
});

const parseRRULE = (rruleString) => {
    if (!rruleString) {
      return {};
    }
    
    const result = {};
    const parts = rruleString.split(';');
    
    parts.forEach(part => {
      const [key, value] = part.split('=');
      
      switch (key) {
        case 'FREQ':
          result.repeat = value.toLowerCase();
          break;
        case 'INTERVAL':
          result.interval = parseInt(value);
          break;
        case 'COUNT':
          result.count = parseInt(value);
          break;
        case 'UNTIL':
          result.until = new Date(value);
          break;
        case 'BYDAY':
          result.weekDays = value;
          break;
      }
    });
    
    return result;
  };

const ReccuringCalendar = () => {
  const [myEvents, setMyEvents] = useState([]);
  const [isOpen, setOpen] = useState(false);
  const [isEdit, setEdit] = useState(false);
  const [tempEvent, setTempEvent] = useState(null);
  const [popupEventTitle, setTitle] = useState('');
  const [popupEventDescription, setDescription] = useState('');
  const [popupEventAllDay, setAllDay] = useState(true);
  const [popupEventDate, setDate] = useState([]);
  const [selectedResource, setSelectedResource] = useState(null);
  
  // Recurring event states
  const [isRecurring, setIsRecurring] = useState(false);
  const [recurringType, setRecurringType] = useState('daily');
  const [recurringInterval, setRecurringInterval] = useState(1);
  const [recurringEndType, setRecurringEndType] = useState('never');
  const [recurringEndValue, setRecurringEndValue] = useState(null);
  const [recurringDays, setRecurringDays] = useState(['MO', 'TU', 'WE', 'TH', 'FR']);
  
  const myResources = [
    { id: 1, name: 'Admin 1', color: '#ff0000' },
    { id: 2, name: 'Admin 2', color: '#00ff00' },
    { id: 3, name: 'Admin 3', color: '#0000ff' },
  ];

  // View configuration with days only
  const view = useMemo(() => {
    return {
      timeline: {
        type: 'day',
        size: 7,
        resolutionHorizontal: 'day',
        timeCellStep: 1440,
        timeLabelStep: 1440,
      }
    };
  }, []);

  const resetFormData = useCallback(() => {
    setTitle('');
    setDescription('');
    setDate([]);
    setAllDay(true);
    setSelectedResource(null);
    setIsRecurring(false);
    setRecurringType('daily');
    setRecurringInterval(1);
    setRecurringEndType('never');
    setRecurringEndValue(null);
    setRecurringDays(['MO', 'TU', 'WE', 'TH', 'FR']);
  }, []);

  const onEventClick = useCallback((event) => {
    console.log('Event clicked:', event.event);
    setEdit(true);
    setTempEvent(event.event);
    
    // Fill form with event data
    setTitle(event.event.title || '');
    setDescription(event.event.description || '');
    setDate([event.event.start, event.event.end]);
    setAllDay(event.event.allDay || true);
    setSelectedResource(event.event.resource);
    
    // Handle recurring data
    if (event.event.recurring) {
      setIsRecurring(true);
      
      const recurring = typeof event.event.recurring === 'string' 
        ? parseRRULE(event.event.recurring) 
        : event.event.recurring;
      
      setRecurringType(recurring.repeat || 'daily');
      setRecurringInterval(recurring.interval || 1);
      
      if (recurring.count) {
        setRecurringEndType('count');
        setRecurringEndValue(recurring.count);
      } else if (recurring.until) {
        setRecurringEndType('until');
        setRecurringEndValue(new Date(recurring.until));
      } else {
        setRecurringEndType('never');
        setRecurringEndValue(null);
      }
      
      if (recurring.weekDays) {
        setRecurringDays(recurring.weekDays.split(','));
      }
    } else {
      setIsRecurring(false);
    }
    
    // Open popup
    setOpen(true);
  }, []);

  const onEventCreated = useCallback((args) => {
    console.log('Event created:', args.event);
    setEdit(false);
    // Generate unique ID for new event
    const newId = new Date().getTime().toString();
    const newEvent = {
      ...args.event,
      id: newId,
      allDay: true,
      resource: args.event.resource || myResources[0].id
    };
    
    setTempEvent(newEvent);
    
    // Fill form with basic data
    setTitle('');
    setDescription('');
    setDate([newEvent.start, newEvent.end]);
    setAllDay(true);
    setSelectedResource(newEvent.resource);
    setIsRecurring(false);
    
    // Open popup
    setOpen(true);
  }, []);

  // Helper function to build recurring rule
  const buildRecurringRule = useCallback(() => {
    if (!isRecurring) return null;
    
    const rule = {
      repeat: recurringType,
      interval: recurringInterval
    };
    
    if (recurringType === 'weekly' && recurringDays.length) {
      rule.weekDays = recurringDays.join(',');
    }
    
    if (recurringEndType === 'count' && recurringEndValue) {
      rule.count = recurringEndValue;
    } else if (recurringEndType === 'until' && recurringEndValue) {
      rule.until = recurringEndValue;
    }
    
    return rule;
  }, [isRecurring, recurringType, recurringInterval, recurringDays, recurringEndType, recurringEndValue]);

  const saveEvent = useCallback(() => {
    if (!popupEventTitle.trim()) {
      console.error('Error: Title is required');
      return;
    }

    if (!popupEventDate || popupEventDate.length < 2) {
      console.error('Error: Date is required');
      return;
    }

    const recurringRule = buildRecurringRule();
    
    const newEvent = {
      id: tempEvent.id,
      title: popupEventTitle.trim(),
      description: popupEventDescription.trim(),
      start: popupEventDate[0],
      end: popupEventDate[1],
      allDay: popupEventAllDay,
      resource: selectedResource || tempEvent.resource,
      recurring: recurringRule
    };
    
    console.log('Saving event:', newEvent);
    
    if (isEdit) {
      // Update existing event
      const index = myEvents.findIndex(x => x.id === tempEvent.id);
      if (index >= 0) {
        const newEventList = [...myEvents];
        newEventList.splice(index, 1, newEvent);
        setMyEvents(newEventList);
        console.log('Shift updated');
      }
    } else {
      // Add new event
      setMyEvents(prev => [...prev, newEvent]);
      console.log('Shift added');
    }
    
    // Close popup and reset form
    setOpen(false);
    resetFormData();
  }, [isEdit, myEvents, popupEventAllDay, popupEventDate, popupEventDescription, popupEventTitle, tempEvent, selectedResource, resetFormData, buildRecurringRule]);

  const deleteEvent = useCallback(() => {
    if (!tempEvent || !tempEvent.id) return;
    
    console.log('Deleting shift:', tempEvent);
    setMyEvents(prev => prev.filter(item => item.id !== tempEvent.id));
    setOpen(false);
    resetFormData();
    console.log('Shift deleted');
  }, [tempEvent, resetFormData]);

  const onClosePopup = useCallback(() => {
    setOpen(false);
    resetFormData();
  }, [resetFormData]);

  const handleResourceChange = useCallback((e) => {
    setSelectedResource(parseInt(e.target.value));
  }, []);

  return (
    <div>
      <Eventcalendar
        view={view}
        data={myEvents}
        resources={myResources}
        clickToCreate={true}
        dragToCreate={true}
        dragToMove={true}
        dragToResize={false}
        onEventClick={onEventClick}
        onEventCreated={onEventCreated}
      />
      
      <Popup
        display="center"
        fullScreen={true}
        contentPadding={false}
        headerText={isEdit ? 'Edit Shift' : 'New Shift'}
        isOpen={isOpen}
        onClose={onClosePopup}
        buttons={[
          {
            text: 'Cancel',
            handler: onClosePopup
          },
          {
            text: 'Save',
            handler: saveEvent,
            keyCode: 'enter'
          }
        ]}
      >
        <div className="mbsc-form-group">
          <div className="mbsc-form-group-title">Shift Details</div>
          <Input
            label="Title"
            value={popupEventTitle}
            onChange={(e) => setTitle(e.target.value)}
          />
          <Textarea
            label="Description"
            value={popupEventDescription}
            onChange={(e) => setDescription(e.target.value)}
          />
          <Switch 
            checked={popupEventAllDay}
            onChange={(e) => setAllDay(e.target.checked)}
          >
            All-day
          </Switch>
          
          <Datepicker
            select="range"
            touchUi={true}
            showRangeLabels={false}
            onChange={(args) => setDate(args.value)}
            value={popupEventDate}
            controls={['calendar']}
            display="inline"
          />
          
          <div className="mbsc-form-group-title">Administrator</div>
          <select 
            className="mbsc-select" 
            value={selectedResource || (tempEvent && tempEvent.resource) || ''}
            onChange={handleResourceChange}
          >
            <option value="" disabled>Select administrator</option>
            {myResources.map(resource => (
              <option key={resource.id} value={resource.id}>
                {resource.name}
              </option>
            ))}
          </select>
          
          <div className="mbsc-form-group-title">Recurrence</div>
          <Switch 
            checked={isRecurring}
            onChange={(e) => setIsRecurring(e.target.checked)}
          >
            Recurring event
          </Switch>
          
          {isRecurring && (
            <div className="mbsc-form-group">
              <SegmentedGroup value={recurringType} onChange={(e) => setRecurringType(e.target.value)}>
                <SegmentedItem value="daily">Daily</SegmentedItem>
                <SegmentedItem value="weekly">Weekly</SegmentedItem>
                <SegmentedItem value="monthly">Monthly</SegmentedItem>
                <SegmentedItem value="yearly">Yearly</SegmentedItem>
              </SegmentedGroup>
              
              <Input
                label="Repeat every"
                value={recurringInterval}
                type="number"
                min="1"
                onChange={(e) => setRecurringInterval(parseInt(e.target.value) || 1)}
              />
              
              {recurringType === 'weekly' && (
                <div className="mbsc-form-group-title">Repeat on</div>
              )}
              
              {recurringType === 'weekly' && (
                <div className="mbsc-form-group">
                  <SegmentedGroup 
                    select="multiple" 
                    value={recurringDays}
                    onChange={(e) => setRecurringDays(e.value)}
                  >
                    <SegmentedItem value="MO">M</SegmentedItem>
                    <SegmentedItem value="TU">T</SegmentedItem>
                    <SegmentedItem value="WE">W</SegmentedItem>
                    <SegmentedItem value="TH">T</SegmentedItem>
                    <SegmentedItem value="FR">F</SegmentedItem>
                    <SegmentedItem value="SA">S</SegmentedItem>
                    <SegmentedItem value="SU">S</SegmentedItem>
                  </SegmentedGroup>
                </div>
              )}
              
              <div className="mbsc-form-group-title">Ends</div>
              <SegmentedGroup value={recurringEndType} onChange={(e) => setRecurringEndType(e.target.value)}>
                <SegmentedItem value="never">Never</SegmentedItem>
                <SegmentedItem value="count">After</SegmentedItem>
                <SegmentedItem value="until">On date</SegmentedItem>
              </SegmentedGroup>
              
              {recurringEndType === 'count' && (
                <Input
                  label="Occurrences"
                  value={recurringEndValue || ''}
                  type="number"
                  min="1"
                  onChange={(e) => setRecurringEndValue(parseInt(e.target.value) || null)}
                />
              )}
              
              {recurringEndType === 'until' && (
                <Datepicker
                  label="End date"
                  value={recurringEndValue}
                  onChange={(e) => setRecurringEndValue(e.value)}
                />
              )}
            </div>
          )}
        </div>
        
        {isEdit && 
          <div className="mbsc-button-group">
            <Button 
              className="mbsc-button-block" 
              color="danger" 
              variant="outline" 
              onClick={deleteEvent}
            >
              Delete Shift
            </Button>
          </div>
        }
      </Popup>
    </div>
  );
}

export default ReccuringCalendar;
