import React, { useState, useCallback, useMemo } from "react";
import {
  Eventcalendar,
  setOptions,
  Popup,
  Button,
  Switch,
  Datepicker,
  SegmentedGroup,
  SegmentedItem,
  Input,
  localePl,
  Toast,
} from "@mobiscroll/react";
import "@mobiscroll/react/dist/css/mobiscroll.min.css";

setOptions({
  theme: "ios",
  themeVariant: "light",
});

// const Responsiveness = {

// } {

const DarkModeButtons = () => (
  <>
    <Button
      onClick={() => setOptions({ theme: "ios", themeVariant: "light" })}
      color="light"
    >
      Light mode
    </Button>

    <Button
      onClick={() => setOptions({ theme: "ios-dark", themeVariant: "dark" })}
      color="dark"
    >
      Dark mode
    </Button>
  </>
);

const NewCalendar12 = () => {
  const [myEvents, setMyEvents] = useState([]);
  const [isOpen, setOpen] = useState(false);
  const [isEdit, setEdit] = useState(false);
  const [tempEvent, setTempEvent] = useState(null);
  const [popupEventTitle, setTitle] = useState("");
  const [popupEventDescription, setDescription] = useState("");
  const [popupEventAllDay, setAllDay] = useState(false);
  const [popupEventDate, setDate] = useState([]);
  const [popupEventStartTime, setStartTime] = useState("");
  const [popupEventEndTime, setEndTime] = useState("");
  const [isRecurring, setIsRecurring] = useState(false);
  const [recurringInterval, setRecurringInterval] = useState(1);
  const [recurringType, setRecurringType] = useState("weekly");
  const [isToastOpen, setToastOpen] = useState(false);
  const [toastMessage, setToastMessage] = useState("");

  const myResources = [
    { id: 1, name: "Admin 1", color: "#ff0000" },
    { id: 2, name: "Admin 2", color: "#00ff00" },
    { id: 3, name: "Admin 3", color: "#0000ff" },
    { id: 4, name: "Admin 4", color: "#ff00ff" },
    { id: 5, name: "Admin 5", color: "#00ffff" },
  ];

  // View configuration with hours support
  const view = useMemo(() => {
    return {
      timeline: {
        type: "month",
        size: 7,
        resolutionHorizontal: "day",
        startDay: 1, // Monday
        endDay: 0, // Sunday
      },
    };
  }, []);

  const resetFormData = useCallback(() => {
    setTitle("");
    setDescription("");
    setDate([]);
    setAllDay(false);
    setStartTime("");
    setEndTime("");
    setIsRecurring(false);
  }, []);

  const formatTime = (date) => {
    if (!date) return "";
    const hours = date.getHours().toString().padStart(2, "0");
    const minutes = date.getMinutes().toString().padStart(2, "0");
    return `${hours}:${minutes}`;
  };

  const parseTimeToDate = (timeString, baseDate) => {
    if (!timeString || !baseDate) return null;

    const [hours, minutes] = timeString.split(":").map(Number);
    const date = new Date(baseDate);
    date.setHours(hours, minutes, 0, 0);
    return date;
  };

  const onEventClick = (event) => {
    console.log("Event clicked:", event.event);
    setEdit(true);
    setTempEvent(event.event);

    // Fill form with event data
    setTitle(event.event.title || "");
    setDescription(event.event.description || "");
    setIsRecurring(!!event.event.recurring);
    setRecurringType(event.event.recurring?.repeat || "weekly");
    setRecurringInterval(event.event.recurring?.interval || 1);

    const startDate = new Date(event.event.start);
    const endDate = new Date(event.event.end);

    setDate([startDate, endDate]);
    setAllDay(event.event.allDay || false);

    if (!event.event.allDay) {
      setStartTime(formatTime(startDate));
      setEndTime(formatTime(endDate));
    } else {
      setStartTime("");
      setEndTime("");
    }

    // Open popup
    setOpen(true);
  };

  const onEventCreated = (args) => {
    console.log("Event created:", args.event);
    setEdit(false);
    // Generate unique ID for new event
    const newId = new Date().getTime().toString();

    const startDate = new Date(args.event.start);
    const endDate = new Date(args.event.end);

    const newEvent = {
      ...args.event,
      id: newId,
      allDay: false,
      resource: args.event.resource || myResources[0].id,
    };

    setTempEvent(newEvent);

    // Fill form with basic data
    setTitle("");
    setDescription("");
    setDate([startDate, endDate]);
    setAllDay(false);
    setStartTime(formatTime(startDate));
    setEndTime(formatTime(endDate));

    // Open popup
    setOpen(true);
  };

  const onEventUpdated = (args) => {
    console.log("Event updated:", args.event);
    console.log("args", args);
    setEdit(true);
    setTempEvent(args.event);

    // Fill form with event data
    setTitle(args.event.title || "");
    setDescription(args.event.description || "");
    const startDate = new Date(args.event.start);
    const endDate = new Date(args.event.end);
    setIsRecurring(!!args.event.recurring);
    setRecurringType(args.event.recurring?.repeat || "weekly");
    setRecurringInterval(args.event.recurring?.interval || 1);

    setDate([startDate, endDate]);
    setAllDay(args.event.allDay || false);

    if (!args.event.allDay) {
      setStartTime(formatTime(startDate));
      setEndTime(formatTime(endDate));
    } else {
      setStartTime("");
      setEndTime("");
    }

    // Open popup
    setOpen(true);
  };

  const saveEvent = () => {
    if (!popupEventTitle.trim()) {
      console.error("Error: Title is required");
      return;
    }

    if (!popupEventDate || popupEventDate.length < 2) {
      console.error("Error: Date is required");
      return;
    }

    let startDate = new Date(popupEventDate[0]);
    let endDate = new Date(popupEventDate[1]);

    // If not all-day event, set the specific times
    if (!popupEventAllDay) {
      if (!popupEventStartTime) {
        console.error("Error: Start time is required for non-all-day events");
        return;
      }

      if (!popupEventEndTime) {
        console.error("Error: End time is required for non-all-day events");
        return;
      }

      startDate = parseTimeToDate(popupEventStartTime, startDate);
      endDate = parseTimeToDate(popupEventEndTime, endDate);

      if (startDate >= endDate) {
        console.error("Error: End time must be after start time");
        return;
      }
    } else {
      // For all-day events, ensure time is set to 00:00
      startDate.setHours(0, 0, 0, 0);
      endDate.setHours(23, 59, 59, 999);
    }
    console.log(isRecurring);

    const newEvent = {
      id: tempEvent.id,
      title: popupEventTitle.trim(),
      description: popupEventDescription.trim(),
      start: startDate,
      end: endDate,
      allDay: popupEventAllDay,
      resource: tempEvent.resource,
      ...(isRecurring
        ? { recurring: { repeat: recurringType, interval: recurringInterval } }
        : {}),
    };

    console.log("Saving event:", newEvent);

    if (isEdit) {
      // Update existing event
      const index = myEvents.findIndex((x) => x.id === tempEvent.id);
      if (index >= 0) {
        const newEventList = [...myEvents];
        newEventList.splice(index, 1, newEvent);
        setMyEvents(newEventList);
        console.log("Shift updated");
      }
    } else {
      // Add new event
      setMyEvents((prev) => [...prev, newEvent]);
      console.log("Shift added");
    }

    console.log(["events", myEvents]);

    // Close popup and reset form
    setOpen(false);
    resetFormData();
    setToastMessage("Shift saved");
    setToastOpen(true);
  };

  const deleteEvent = useCallback(() => {
    if (!tempEvent || !tempEvent.id) return;

    console.log("Deleting shift:", tempEvent);
    setMyEvents((prev) => prev.filter((item) => item.id !== tempEvent.id));
    setOpen(false);
    resetFormData();
    setToastMessage("Shift deleted");
    setToastOpen(true);
  }, [tempEvent, resetFormData]);

  const handleEventDeleted = useCallback(
    (args) => {
      deleteEvent(args.event);
    },
    [deleteEvent]
  );

  const handleToastClose = useCallback(() => {
    setToastOpen(false);
    setToastMessage("");
  }, []);

  const handlePopupClose = useCallback(() => {
    if (!isEdit) {
      // Remove event if popup is cancelled
      setMyEvents([...myEvents]);
    }
    setTempEvent(null);
    setOpen(false);
  }, [isEdit, myEvents]);

  return (
    <div>
      <DarkModeButtons />
      <Eventcalendar
        view={view}
        data={myEvents}
        resources={myResources}
        clickToCreate={true}
        dragToCreate={true}
        dragToMove={true}
        dragToResize={true}
        onEventClick={onEventClick}
        onEventCreated={onEventCreated}
        onEventUpdated={onEventUpdated}
        onEventDeleted={handleEventDeleted}
        locale={localePl}
        responsive={{
          xsmall: {
            view: {
              // timeline: {
              //   type: "day",
              //   resolutionHorizontal: "day",
              //   size: 7,
              // },
              schedule:{
                type: 'day',
                // size: 1,
                // resolutionHorizontal: 'hour',
              }
            },
          },
          medium: {
            view: {
              timeline: {
                type: "week",
                resolutionHorizontal: "day",
                size: 7,
              },
            },
          },
          xlarge: {
            view: {
              timeline: {
                type: "month",
                size: 2,
                resolutionHorizontal: "day",
                // startDay: 1, // Monday
                // endDay: 0, // Sunday
              },
            },
          },
        }}
      />

      <Popup
        display="center"
        fullScreen={true}
        contentPadding={false}
        headerText={isEdit ? "Edit Shift" : "New Shift"}
        isOpen={isOpen}
        onClose={handlePopupClose}
        touchUi={true}
        buttons={[
          {
            text: "Cancel",
            handler: handlePopupClose,
          },
          {
            text: "Save",
            handler: saveEvent,
            keyCode: "enter",
          },
        ]}
      >
        <div className="mbsc-form-group">
          <div className="mbsc-form-group-title">Shift Details</div>
          <Input
            label="Title"
            value={popupEventTitle}
            onChange={(e) => setTitle(e.target.value)}
            class="mbsc-input"
          />
          <Input
            label="Description"
            value={popupEventDescription}
            onChange={(e) => setDescription(e.target.value)}
            class="mbsc-input"
          ></Input>
          <Switch
            checked={popupEventAllDay}
            onChange={(e) => setAllDay(e.target.checked)}
          >
            All-day
          </Switch>

          <Datepicker
            select="range"
            showRangeLabels={false}
            onChange={(args) => setDate(args.value)}
            value={popupEventDate}
            controls={["calendar"]}
          />

          {!popupEventAllDay && (
            <div className="mbsc-form-group">
              <div className="mbsc-form-group-title">Time</div>
              <div className="mbsc-form-group-row">
                <Input
                  label="Start Time"
                  value={popupEventStartTime}
                  onChange={(e) => setStartTime(e.target.value)}
                  type="time"
                />
                <Input
                  label="End Time"
                  value={popupEventEndTime}
                  onChange={(e) => setEndTime(e.target.value)}
                  type="time"
                />
              </div>
            </div>
          )}

          {/* <div className="mbsc-form-group-title">Administrator</div> */}
          {/* <select 
            className="mbsc-select" 
            value={selectedResource || (tempEvent && tempEvent.resource) || ''}
            onChange={handleResourceChange}
          >
            <option value="" disabled>Select administrator</option>
            {myResources.map(resource => (
              <option key={resource.id} value={resource.id}>
                {resource.name}
              </option>
            ))}
          </select> */}
        </div>
        <div className="mbsc-form-group-title">Recurrence</div>
        <Switch
          checked={isRecurring}
          onChange={(e) => setIsRecurring(e.target.checked)}
        >
          Recurring event
        </Switch>

        {isRecurring && (
          <div className="mbsc-form-group">
            <SegmentedGroup
              value={recurringType}
              onChange={(e) => setRecurringType(e.target.value)}
            >
              <SegmentedItem value="weekly">Weekly</SegmentedItem>
              <SegmentedItem value="monthly">Monthly</SegmentedItem>
              <SegmentedItem value="yearly">Yearly</SegmentedItem>
            </SegmentedGroup>

            <Input
              label="Repeat every"
              value={recurringInterval}
              type="number"
              min="1"
              onChange={(e) =>
                setRecurringInterval(parseInt(e.target.value) || 1)
              }
            />
          </div>
        )}

        {isEdit && (
          <div className="mbsc-button-group">
            <Button
              className="mbsc-button-block"
              color="danger"
              variant="outline"
              onClick={handleEventDeleted}
            >
              Delete Shift
            </Button>
          </div>
        )}
      </Popup>
      <h1>Events:</h1>
      <pre>{JSON.stringify(myEvents, null, 2)}</pre>
      <h1>Resources</h1>
      <pre>{JSON.stringify(myResources, null, 2)}</pre>
      <Toast
        message={toastMessage}
        isOpen={isToastOpen}
        onClose={handleToastClose}
      />
    </div>
  );
};

export default NewCalendar12;
